body { font-family: Poppins, Helvetica, sans-serif; font-size: 1rem; }

main { margin-top: 1rem; }

h1 { text-align: center; padding-bottom: 30px; margin-bottom: 15px; border-bottom: 1px solid #C0C0C0; font-weight: 600; }
h2 { font-size: 1.4rem; font-weight: 600; }
p { color: #a9aeb1; font-weight: 400; }
a { color: #f1850d; text-decoration: none; border-bottom: 1px solid #f1850d; }
a:hover { color: #f1850d; opacity: 0.7; }

button { width: 100%; border: 0 none; font-size: 1.1rem; line-height: 44px; border-radius: 50px; padding: 12px 15px; color: #FFF; font-weight: 600; box-shadow: 8px 10px 30px -15px #c0c0c0; background: rgb(241,125,0); background: linear-gradient(90deg, rgba(241,125,0,1) 50%, rgba(245,147,40,1) 100%); transition: all 0.3s ease-in-out; }
button:hover { transform: translateY(5px); }
button > i { padding-right: 4px; }
button .button--arrow { float: right; background-color: #d07b1e; border-radius: 50%; display:block; width: 44px; height: 44px; }
button .button--arrow i { line-height: 44px; }

.form-control { padding: 15px 10px; border-radius: 2px; margin-bottom: 10px; }
.form-select { padding: 15px 10px; border-radius: 2px; margin-bottom: 10px; color: #6c757d; }
.form-check { padding: 0; margin: 0; background-color: #f7f8f8; border: 2px solid #f7f8f8; }
.form-check:last-of-type { margin-bottom: 10px; }
.form-check--active { border: 2px solid #f1850d; background-color: transparent; }
.form-check label { color: #212529; font-weight: 600; font-size: 0.9rem; display: block; padding: 15px; }
.form-group { display: flex; justify-content: space-between; }
.form-group input, .form-group select { margin-right: 10px; }
.form-group input:last-child, .form-group select:last-child { margin-right: 0; }

.step { margin-bottom: 45px; }

.checkout__total { display: flex; align-items: center; justify-content: space-between; margin-top: 15px; }
.checkout__total strong { color: #212529; }

.checkout__promotion { color: #a9aeb1; font-size: 0.9rem; line-height: 1.3; display: flex;  margin-top: 15px; border-radius: 4px; padding: 15px; background: rgb(91,19,92); background: linear-gradient(90deg, rgba(91,19,92,1) 0%, rgba(128,0,127,1) 100%); }
.checkout__promotion .bell { color: #d07b1e; font-size: 1.5rem; padding: 5px 15px 0 0; }
.checkout__promotion p { color: #fff; font-weight: 600; font-size: 1rem; margin-bottom: 5px; }

.step-shader { color: #aaa; background-color: #eff2f3; padding: 15px 20px; border-radius: 4px; }
.step-shader h2 { text-align: center; font-weight: 400; font-size: 1.3rem; margin-bottom: 15px; }
.step-shader h3 { color: #212529; font-size: 1.1rem; }

/* special radio button */
.radio-custom { opacity: 0; position: absolute; }
.radio-custom, .radio-custom-label { display: inline-block; vertical-align: middle; margin: 5px; cursor: pointer; }
.radio-custom-label { position: relative; outline: none; }
.radio-custom + .radio-custom-label:before { content: ''; border: 2px solid #d1d2d1; display: inline-block; vertical-align: middle; width: 24px; height: 24px; padding: 2px; margin-right: 10px; text-align: center; }
.radio-custom + .radio-custom-label:before { border-radius: 50%; }
.radio-custom:checked + .radio-custom-label:before { background: #f1850d; border: 2px solid #f1850d; content: "\f00c"; font-family: 'FontAwesome'; color: #fff; }
.radio-custom:focus + .radio-custom-label { outline: 1px solid #fff; }

@media screen and (min-width: 991px) {
    main { margin-top: 3rem; }
}